.quality__item {
  width: 558px;
  height: 199px;
  background-color: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 100;
  margin-bottom: 27px;
}

.quality__container {
  background-color: #eab734;
  display: flex;
  justify-content: space-between;
  width: 1150px;
  flex-wrap: wrap;
}

.quality {
  background-color: #eab734;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 80px;
}

.quality__item p {
  color: black;
  max-width: 353px;
}


.quality__item h4 {
  position: absolute;
  top: -2px;
  left: 50px;
  font-weight: 900;
  font-size: 170px;
  line-height: 199px;
  z-index: 0;
  opacity: 0.4;
  color: #eab734;
}

.quality__item h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  padding-bottom: 14px;
}


.quality h2 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 92px;
  padding-bottom: 35px;
}
