.service {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 120px;
}

.service__container {
  width: 100%;
  max-width: $container;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service__item {
  width: 264px;
  height: 134px;
  border: 1px solid #CECDCD;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 34px;
}

.service__item h3 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  max-width: 214px;
}


.service__popup-block {
  display: none;
  width: 264px;
  height: 134px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background: url("../images/content/bg-service.jpg") no-repeat;
  background-size: 264px 134px;
  padding: 5px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
}

.service__item:hover .service__popup-block {
  display: flex;
}


.service__link {
  width: 156px;
  height: 39px;
  color: black;
  border-radius: 50px;
  background-color: #eab734;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    background-color: #f06543;
  }
}


.service h2 {
  padding-top: 101px;
  padding-bottom: 61px;
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
}

.service__button {
  background: none;
  border: 1px solid #101010;
  width: 156px;
  height: 39px;
  border-radius: 50px;

  &:hover {
    background-color: #eab734;
  }
}
