html {
  font-family: $fontfam;
  font-size: 16px;
  height: 100%;
}

body {
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul,
a {
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

a:visited {
  text-decoration: none;
}

p {
  color: #ffffff;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  font-family: $fontfam;
}

input::-ms-clear {
  display: none;
}

input:invalid {
  box-shadow: none;
}

button,
input {
  border: none;
}

audio,
canvas,
iframe,
img,
svg,
video,
textarea {
  vertical-align: middle;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}


.chosen-choices {
  border-radius: 3px;
  box-shadow: inset 0 1px 2px rgba(0,0,0,0.3);
  border: none;
  cursor: text;
  margin-top: 12px;
  padding-left: 15px;
  border-bottom: 1px solid #eab734;
  width: 285px;
  text-indent: 0;
  margin-left: 30px;
  background-color: #EAB734;
}
