@mixin font-face($font-name, $path, $weight: normal, $style: normal, $option: fallback) {
  @font-face {
    font-family: quote($font-name);
    src:
      url(#{$path}.woff2) format("woff2"),
      url(#{$path}.woff) format("woff"),
      url(#{$path}.ttf) format("truetype");
    font-weight: $weight;
    font-style: $style;
    font-display: $option;
  }
}

@include font-face('Roboto', '../fonts/roboto/roboto/roboto', 400, 'normal', 'fallback');
@include font-face('Roboto', '../fonts/roboto/robotolight/robotolight', 300, 'normal', 'fallback');
@include font-face('Roboto', '../fonts/roboto/robotobold/robotobold', 600, 'bold', 'fallback');
@include font-face('FireSans', '../fonts/firasans/firesans', 400, 'normal', 'fallback');
@include font-face('Montserrat', '../fonts/firesans/firesans', 300, 'normal', 'fallback');

