.advantages__item svg {
  width: 80px;
  height: 80px;
  fill: #eab734;
}

.advantages h2 {
  color: white;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  padding-top: 92px;
  padding-bottom: 61px;
}

.advantages__item p {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: black;
  max-width: 288px;
  padding-top: 34px;
}

.advantages__item h3 {
  padding-top: 36px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
}

.advantages__item {
  width: 362px;
  height: 359px;
  background: white;
  position: relative;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 36px;
}

.advantages {
  background: black;
}

.advantages__container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  //margin-left: 227px;
  //margin-right: 225px;
  justify-content: space-around;
  max-width: 1200px;
}
