.modal {
  display: none;
}

.modal::before {
  content: "";
  background: black;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  opacity: 0.9;
  height: 100%;
  z-index: 9999;
}

.modal__container {
  position: fixed;
  top: 15%;
  width: 100%;
  height: 100%;
  background: black;
  outline: none;
  border-radius: 4px;
  z-index: 99999;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}


.modal__close {
  position: absolute;
  top: 24px;
  right: 94px;
  padding: 10px;

  &::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 4px;
    transform: rotate(45deg);
    background: #EAB734;
    top: 17px;
    right: 2px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 17px;
    right: 2px;
    width: 30px;
    height: 4px;
    transform: rotate(-45deg);
    background: #EAB734;
  }
}

.modal__container h3 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  color: #eab734;
  padding-bottom: 40px;
}

.modal__container p {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: white;
  max-width: 554px;
  padding-bottom: 30px;
}

body.modal {
  overflow: hidden;
}

@media (max-width: $tablet-width) {
  .modal__container {
    left: 27%;
  }
}

@media (max-width: $mobile-width) {
  .modal__container {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
  }

  .modal__container p {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    max-width: 259px;
  }

  .modal__container h3 {
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
  }
}
