.header-content__block {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 540px;
  margin-left: 122px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 5px;
    background-color: #EAB734;
    border-radius: 40px 0 0 40px;
    left: -17px;
  }
}

.header-content__footer {
  padding-top: 72px;
  padding-bottom: 40px;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 23px;
  max-width: 430px;
}

.header-content__header {
  color: white;
  padding-top: 26px;
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 29px;
}

.header-content__footer span {
  color: #eab734;
}

.header-content__block h2 {
  color: white;
  padding-top: 62px;
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  max-width: 448px;
}

.header-content {
  padding-top: 120px;
  padding-bottom: 115px;
  background: url("../images/content/header-bg.png") no-repeat;
  //background-size: 800px 440px;
  background-color: black;
  background-position: right;
  background-position-y: -28px;
  position: relative;
  z-index: 100;
}

.header-content__form-block {
  padding-top: 345px;
}

.header-content__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-content__form input {
  background: #EAB734;
  padding: 20px;
  border-radius: 30px;
  margin-bottom: 31px;

  &::placeholder {
    font-family: $fontfam;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 18px;
    color: black;
  }

  &:hover {
    background-color: white;
  }

  //&:last-child {
  //  margin-bottom: 0;
  //}
}

.header-content__form-header {
  display: flex;
  flex-direction: column;
  max-width: 1140px;
  width: 100%;
}


.header-content__form-control {
  background: #eab734;
  padding: 20px;
  border-radius: 30px;
  padding-left: 30px;
  border: none;
  width: 265px;
}

.header-content__select {
  width: 265px;
  border-radius: 30px;
  height: 65px;
  display: flex;
  flex-direction: column;
  padding-left: 27px;
  justify-content: center;
  position: relative;
  background: #EAB734;
  border: 1px solid #EAB734;
  margin-bottom: 14px;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: black;
    transform: rotate(45deg);
    right: 39px;
    top: 29px;

    transition: 0.3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: black;
    transform: rotate(-45deg);
    right: 40px;
    top: 37px;
    transition: 0.3s ease;
  }

  &.is-open {
    .select-body {
      display: block;
    }
  }

  &-header {
    cursor: pointer;
  }

  &-body {
    display: none;
    flex-direction: column;
    left: -1px;
    position: absolute;
    top: 100%;
    right: 0;
    border: 1px solid #eab734;
    border-radius: 0 0 25px 25px;
    background: white;
    z-index: 1000;
    align-items: center;
    padding-top: 20px;
    padding-left: 5px;
  }

  &-item {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 12px;
    border-radius: 5px;
    margin-bottom: 10px;
    margin-right: 10px;
    font-family: $fontfam;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */

    text-align: center;

    &:hover {
      background-color: #EAB734;
    }
  }
}

.select.is-active .header-content__select-body {
  display: flex;
  border-top: 0;
  width: 265px;
}

.header-content__select.is-active {
  border-radius: 30px 30px 0 0;
  background-color: white;

  &::before {
    right: 48px;
    top: 37px;
  }
}

.header-content__btn-sub {
  width: 358px;
  height: 65px;
  background: black;
  border: 2px solid #eab734;
  color: white;
  padding: 20px;
  border-radius: 50px;

  &:hover {
    background: #eab734;
  }
}

#tel {
  width: 460px;
}

.header-content__form-class {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1140px;
}

.header-content__option {
  font-family: $fontfam;
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  padding: 15px;
}

//.header-content__select {
//  position: relative;
//  display: inline-block;
//}

.header-content__main-select {
  padding-right: 25px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.header-content__bg-mob {
  display: none;
}

@media (max-width: 1330px) {
  .header-content__block {
    margin-left: 20px;
  }

  .header-content {
    background-size: contain;
  }
}

@media (max-width: $tablet-width) {
  .header-content {
    padding-top: 0;
    background: none;
    background-color: black;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .header-content__form-block {
    padding-top: 100px;
  }

  .header-content__form-class {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  #tel {
    width: 265px;
  }

  .header-content__block {
    align-items: center;

    &::before {
      display: none;
    }
  }

  .header-content__block h2 {
    text-align: center;
  }

  .header-content__block p {
    text-align: center;
  }
}

@media (max-width: $mobile-width) {
  .header-content__header {
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    margin-left: 84px;
    margin-right: 84px;
  }

  .header-content__block h2 {
    font-weight: 900;
    font-size: 22px;
    line-height: 26px;
    text-align: center;
    margin-right: 48px;
    margin-left: 48px;
  }

  .header-content__footer {
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    margin-left: 46px;
    margin-right: 46px;
  }

  .header-content__block {
    margin-left: 0;
  }

  .header-content__form input {
    display: none;
  }

  .header-content__form-control {
    display: none;
  }

  .header-content__btn-sub {
    background: #eab734;
    width: 264px;

    &:hover {
      background: black;
    }
  }

  .header-content__form-block {
    padding-top: 20px;
  }

  .header-content {
    padding-bottom: 30px;
  }

  .header-content__bg-mob {
    width: 264px;
    height: 200px;
    display: block;
    background: url("../images/content/header-bg-mob.png") no-repeat;
    background-color: black;
    background-position: center;
  }
}
