.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form h2 {
  padding-top: 100px;
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */

  text-align: center;
}

.form p {
  font-family: $fontfam;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  color: black;
  max-width: 478px;
  padding-bottom: 64px;
}


.form__main input {
  border: 1px solid #eab734;
  border-radius: 30px;
  width: 263px;
  padding: 15px;

  &::placeholder {
    color: #7E7E7E;
  }
}

#phone {
  width: 463px;
}

.form__btn {
  width: 361px;
  border-radius: 50px;
  background: #eab734;
  color: black;
  padding: 15px;

  &:hover {
    background-color: orange;
  }
}

.form__main {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1150px;
  padding-bottom: 104px;
}
