.servtype {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servtype__container {
  display: flex;
  width: 100%;
  max-width: 1300px;
  justify-content: space-between;
  margin-top: 100px;
  padding-bottom: 100px;
}

.servtype__item {
  position: relative;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;

  &::before {
    content: "";
    position: absolute;
    width: 300px;
    height: 300px;
    top: -78px;
    left: 79px;
    background: #eab734;
    border-radius: 50%;
    z-index: 10;
  }
}


.servtype__item img {
  z-index: 1000;
  position: relative;
}


.servtype__item h3 {
  padding-top: 40px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  max-width: 211px;
}

.servtype__btn {
  width: 264px;
  height: 65px;
  border-radius: 30px;
  color: black;
  background: #eab734;
  margin-top: 28px;
}


.servtype__link {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: $fontfam;
  color: black;
  border: 2px solid #eab734;
  text-align: center;
  border-radius: 30px;
  width: 264px;
  height: 65px;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  /* identical to box height */
}


.servtype h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  padding-top: 107px;
  padding-bottom: 52px;
}
