/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: flex;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
    float: right;
  }
  img {
    display: block;
  }
  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}
.slick-arrow.slick-hidden {
  display: none;
}

.slick-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
}

.slider .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 58px;
}

.slider .slick-dots li {
  list-style: none;
  margin: 0 2px;
}

.slider .slick-dots button {
  font-size: 0;
  width: 15px;
  height: 15px;
  background-color: black;
  border-radius: 50%;
}

.slider .slick-dots li.slick-active button{
  background-color: #eab734;
}

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  margin: -30px 0px 0px 0px;
  z-index: 10;
  font-size: 0;
  width: 30px;
  height: 60px;
  background-color: white;
}


.slider .slick-arrow.slick-prev {
  left: -40px;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: black;
    transform: rotate(45deg);
    right: 9px;
    top: 28px;

    transition: 0.3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: black;
    transform: rotate(-40deg);
    right: 9px;
    top: 20px;
    transition: 0.3s ease;
  }
}
.slider .slick-arrow.slick-next {
  right: -40px;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    top: 28px;
    background-color: black;
    transform: rotate(-45deg);
    right: 9px;
    transition: 0.3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: black;
    transform: rotate(40deg);
    right: 9px;
    top: 20px;
    transition: 0.3s ease;
  }
}
.slider .slick-arrow.slick-disabled {
  opacity: 0.2;
}
