.slider__item {
  border-radius: 25px;
  background-color: #eab734;
  width: 558px;
  height: 378px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.slider__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: $container;
  margin: 0 -15px;
}

.slider__item h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding-bottom: 10px;
}

.slider__item h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding-bottom: 30px;
}

.slider__item p {
  font-family: $fontfamM;
  font-style: italic;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: black;
  margin: 0 20px;
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 100px;
}

.slider h2 {
  font-family: $fontfam;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height */
  padding-bottom: 50px;
  text-align: center;
}

.slider__item img {
  display: flex;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 28px;
  margin-bottom: 20px;
}
