.popup-fade {
  display: none;
}

.popup-fade::before {
  content: "";
  background: black;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  opacity: 0.9;
  height: 100%;
  z-index: 9999;
}

.popup {
  position: fixed;
  top: 15%;
  left: 38%;
  width: 728px;
  height: 444px;
  margin-left: -186px;
  background: black;
  outline: none;
  border-radius: 4px;
  z-index: 99999;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.popup-close {
  position: absolute;
  top: 23px;
  right: 28px;
  padding: 10px;

  &::before {
    content: "";
    position: absolute;
    width: 23px;
    height: 4px;
    transform: rotate(45deg);
    background: #EAB734;
    top: 18px;
    right: 2px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 18px;
    right: 2px;
    width: 23px;
    height: 4px;
    transform: rotate(-45deg);
    background: #EAB734;
  }
}

.popup__photo-block {
  width: 338px;
  height: 444px;
  background: url("../img/bg-modal.jpg");
  background-repeat: no-repeat;
}


.popup__container {
  width: 100%;
  display: flex;
}


.popup__form-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.popup__form-block h3 {
  margin-bottom: 50px;
}

.popup__btn-sub {
  width: 261px;
  height: 65px;
  background: #EAB734;
  color: black;
  font-family: $fontfam;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  border-radius: 50px;
}

.popup__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup__form p {
  text-align: center;
  color: white;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  max-width: 261px;
  padding-top: 17px;
}

.popup__form input {
  width: 264px;
  background: white;
  padding: 20px;
  border-radius: 50px;
  margin-bottom: 17px;
}

body.popup {
  overflow: hidden;
}

.popup__label {
  position: absolute;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  top: 51px;
  left: 0;
  color: red;

  &--tel {
    top: -15px;
  }
}

@media (max-width: $tablet-width) {
  .popup {
    left: 27%;
  }
}

@media (max-width: $mobile-width) {
  .popup {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
  }

  .popup__photo-block {
    display: none;
  }

  .popup__btn-sub {
    width: 261px;
  }
}
